.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.login-form{
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
  height: 100%;
}
.taxt-white{
  font-size: 32px;
  line-height: 10px;
  color: #fff;
}
.sign-up-form{
  width: 100%;
  max-width: 400px;
  padding: 20px;
  margin: auto;
  height: 100%;
}
.form-container{
  background: #fff;
  border-radius: 20px;
  padding: 20px 80px;
  max-width: 800px;
  margin: 0 auto;
  box-shadow: 0 15px 16px ,17px #4286f4;
}
.h1{
  text-align: center;
}
.taxt-white{
  font-size: 32px;
  line-height: 10px;
  color: #fff;
};